// src/utils/supabase.ts

import { createClient, SupabaseClient } from '@supabase/supabase-js'
import {
  Task,
  Project,
  Category,
  DailyPlan,
  JournalEntry
} from '../types'
import { WorkSchedule } from '../types/WorkSchedule'

// ─── Load credentials ───────────────────────────────────────────────
const supabaseUrl = import.meta.env.VITE_SUPABASE_URL
const supabaseKey = import.meta.env.VITE_SUPABASE_KEY

if (!supabaseUrl || !supabaseKey) {
  throw new Error('Missing Supabase URL or Key')
}

// ─── Initialize Supabase client ────────────────────────────────────
let supabase: SupabaseClient

try {
  supabase = createClient(supabaseUrl, supabaseKey)
  console.log('supabase.ts: Supabase client created successfully')
} catch (error) {
  console.error('supabase.ts: ERROR creating Supabase client:', error)
  // Fallback mock client so the app doesn’t crash
  supabase = {
    auth: { getSession: async () => ({ data: null, error: new Error('Supabase unavailable') }) },
    from: () => ({
      select: () => ({ data: null, error: new Error('Supabase unavailable') }),
      delete: () => ({ data: null, error: new Error('Supabase unavailable') }),
      insert: () => ({ data: null, error: new Error('Supabase unavailable') }),
      upsert: () => ({ data: null, error: new Error('Supabase unavailable') }),
    })
  } as unknown as SupabaseClient
}

// ─── Export the single client ──────────────────────────────────────
export { supabase }

// ─── Helper functions ───────────────────────────────────────────────

/**
 * Checks if the Supabase connection is working
 * @returns {Promise<boolean>} True if connected, false otherwise
 */
export const checkSupabaseConnection = async (): Promise<boolean> => {
  try {
    // Simple query to test connection
    const { data, error } = await supabase.from('tasks').select('count').limit(1);

    if (error) {
      console.error('supabase.ts: Connection check failed:', error);
      return false;
    }

    console.log('supabase.ts: Connection check succeeded:', data);
    return true;
  } catch (error) {
    console.error('supabase.ts: Connection check exception:', error);
    return false;
  }
};

/**
 * Initializes Supabase tables if they don't exist
 * @returns {Promise<boolean>} True if successful, false otherwise
 */
export const initializeSupabase = async (): Promise<boolean> => {
  try {
    // We can't directly create tables via the Supabase JS client
    // Tables should be created in the Supabase dashboard
    // This function can be used to verify tables exist or perform other initialization

    // Check if tasks table exists by trying to query it
    const { error: tasksError } = await supabase.from('tasks').select('count').limit(1);
    if (tasksError) {
      console.error('supabase.ts: Tasks table may not exist:', tasksError);
      return false;
    }

    return true;
  } catch (error) {
    console.error('supabase.ts: Error initializing Supabase:', error);
    return false;
  }
};

/**
 * Imports data into Supabase
 * @param {any} data The data to import
 * @returns {Promise<boolean>} True if successful, false otherwise
 */
export const importData = async (data: any): Promise<boolean> => {
  try {
    const { tasks, projects, categories, dailyPlans, journalEntries, workSchedule } = data;

    // Import tasks
    if (tasks && tasks.length > 0) {
      const { error: tasksError } = await supabase.from('tasks').upsert(tasks);
      if (tasksError) {
        console.error('supabase.ts: Error importing tasks:', tasksError);
        return false;
      }
    }

    // Import projects
    if (projects && projects.length > 0) {
      const { error: projectsError } = await supabase.from('projects').upsert(projects);
      if (projectsError) {
        console.error('supabase.ts: Error importing projects:', projectsError);
        return false;
      }
    }

    // Import categories
    if (categories && categories.length > 0) {
      const { error: categoriesError } = await supabase.from('categories').upsert(categories);
      if (categoriesError) {
        console.error('supabase.ts: Error importing categories:', categoriesError);
        return false;
      }
    }

    // Import daily plans
    if (dailyPlans && dailyPlans.length > 0) {
      const { error: dailyPlansError } = await supabase.from('daily_plans').upsert(dailyPlans);
      if (dailyPlansError) {
        console.error('supabase.ts: Error importing daily plans:', dailyPlansError);
        return false;
      }
    }

    // Import journal entries
    if (journalEntries && journalEntries.length > 0) {
      const { error: journalError } = await supabase.from('journal_entries').upsert(journalEntries);
      if (journalError) {
        console.error('supabase.ts: Error importing journal entries:', journalError);
        return false;
      }
    }

    // Import work schedule
    if (workSchedule) {
      const { error: scheduleError } = await supabase.from('work_schedule').upsert([workSchedule]);
      if (scheduleError) {
        console.error('supabase.ts: Error importing work schedule:', scheduleError);
        return false;
      }
    }

    return true;
  } catch (error) {
    console.error('supabase.ts: Error importing data:', error);
    return false;
  }
};

/**
 * Exports data from Supabase
 * @returns {Promise<any>} The exported data
 */
export const exportData = async (): Promise<any> => {
  try {
    // Fetch all data from Supabase
    const [
      { data: tasks, error: tasksError },
      { data: projects, error: projectsError },
      { data: categories, error: categoriesError },
      { data: dailyPlans, error: dailyPlansError },
      { data: journalEntries, error: journalError },
      { data: workSchedule, error: scheduleError }
    ] = await Promise.all([
      supabase.from('tasks').select('*'),
      supabase.from('projects').select('*'),
      supabase.from('categories').select('*'),
      supabase.from('daily_plans').select('*'),
      supabase.from('journal_entries').select('*'),
      supabase.from('work_schedule').select('*')
    ]);

    // Check for errors
    if (tasksError) console.error('supabase.ts: Error exporting tasks:', tasksError);
    if (projectsError) console.error('supabase.ts: Error exporting projects:', projectsError);
    if (categoriesError) console.error('supabase.ts: Error exporting categories:', categoriesError);
    if (dailyPlansError) console.error('supabase.ts: Error exporting daily plans:', dailyPlansError);
    if (journalError) console.error('supabase.ts: Error exporting journal entries:', journalError);
    if (scheduleError) console.error('supabase.ts: Error exporting work schedule:', scheduleError);

    // Return the data
    return {
      tasks: tasks || [],
      projects: projects || [],
      categories: categories || [],
      dailyPlans: dailyPlans || [],
      journalEntries: journalEntries || [],
      workSchedule: workSchedule && workSchedule.length > 0 ? workSchedule[0] : null
    };
  } catch (error) {
    console.error('supabase.ts: Error exporting data:', error);
    throw error;
  }
};